@import '_var.scss';

.header_fon {
 background: #fafcfc;
 min-height:800px;
 display: flex;
 align-items: center;
}


.top_line {
  top:0;
  left:0;
  background-color: #fafcfc;
  height: 100px;
  width: 100%;
  position: fixed;
  z-index: 100;
}

.top_line_box {
  display: flex;
  height: 100px;
  align-items: center;
  justify-content: space-between;
  border-bottom:1px solid #ebebeb;
}

.top_line_logo {
  color: #fff;
}

.top_logo_icon {
  padding: 0;
  margin: 0;
  width: 64px;
  height: 64px;
  float:left;
}
.top_line_text {
  @include font-size(15px);
  line-height: 64px;
  font-family: "HelveticaNeueLTStd-Hv", sans-serif;
  float:left;
  color:#656973;
  padding-left: 15px;
}

.header_box {
    width: 1170px;
    margin: 0 auto;
  }



/* стили оформления контента в хедере */
.header_content {
  // padding: 15% 0;
}

.header_content_item {
  width: 50%;
  margin:0;
  float: left;
}

.header_content_item img {
  display: inline-block;
  width: 100%;
  height: auto;
}
.hc_title {
  border-left:5px solid $acsent_colir;
  padding-left: 30px;
  padding-top: 10px;
  margin-bottom: 60px;
}

.hc_title h1 {
  font-family: "RalewayHeavy", sans-serif;
  @include font-size(48px);
  line-height: (35px / 72px);
  text-transform: uppercase;
  margin-bottom: 23px;
}
.hc_title h2 {
  @include font-size(24px);
  line-height: 1;
  font-family: "RalewayExtraBold", sans-serif;
}
.hc_desc {
  margin-bottom: 44px;
}

.hc_link {
  @include font-size(16px);
  border-radius: 40px;
  display: inline-block;
  background-color: $acsent_colir;
  color:#fff;
  padding: 17px 35px;
  font-family: "LatoBold", sans-serif;
  @include trans;

}
.hc_link:hover {
  background-color: lighten($acsent_colir, 10%);
}

.hc_watch {
