 @include font-size(16px);
 text-align: center;
 font-family: "LatoBold", sans-serif;
 color:#333740;
 text-transform: uppercase;
}

.hc_watch:hover {
color: lighten(#333740, 10%);
text-decoration: underline;
  }
  .hc_watch .fa {
    margin-left: 5px;
    @include font-size(20px);
color:$acsent_colir;
  }





























