*, *:before, *:after {
  box-sizing: border-box;
  outline: none;
  padding: 0;
  margin: 0; }

html {
  box-sizing: border-box;
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.5;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  /* overflow-x: hidden; */ }

body {
  font-family: "LatoLight", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  position: relative;
  min-width: 320px;
  height: 100%;
  margin: 0;
  padding: 0;
  color: #5b5f64;
  opacity: 1;
  -webkit-backface-visibility: hidden;
  -webkit-font-smoothing: antialised;
  overflow-x: hidden; }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0; }

a {
  outline: 0;
  text-decoration: none; }

ul {
  margin: 0;
  padding: 0; }

li {
  list-style-type: none; }

.img-responsive {
  display: block;
  width: 100%;
  height: auto; }

.hidden {
  display: none; }

