.header_fon {
  background: #fafcfc;
  min-height: 800px;
  display: flex;
  align-items: center; }

.top_line {
  top: 0;
  left: 0;
  background-color: #fafcfc;
  height: 100px;
  width: 100%;
  position: fixed;
  z-index: 100; }

.top_line_box {
  display: flex;
  height: 100px;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #ebebeb; }

.top_line_logo {
  color: #fff; }

.top_logo_icon {
  padding: 0;
  margin: 0;
  width: 64px;
  height: 64px;
  float: left; }

.top_line_text {
  /*  // font-size: $size; */
  font-size: 0.9375rem;
  line-height: 64px;
  font-family: "HelveticaNeueLTStd-Hv", sans-serif;
  float: left;
  color: #656973;
  padding-left: 15px; }

.header_box {
  width: 1170px;
  margin: 0 auto; }

/* стили оформления контента в хедере */
.header_content_item {
  width: 50%;
  margin: 0;
  float: left; }

.header_content_item img {
  display: inline-block;
  width: 100%;
  height: auto; }

.hc_title {
  border-left: 5px solid #18a3dd;
  padding-left: 30px;
  padding-top: 10px;
  margin-bottom: 60px; }

.hc_title h1 {
  font-family: "RalewayHeavy", sans-serif;
  /*  // font-size: $size; */
  font-size: 3rem;
  line-height: 0.48611;
  text-transform: uppercase;
  margin-bottom: 23px; }

.hc_title h2 {
  /*  // font-size: $size; */
  font-size: 1.5rem;
  line-height: 1;
  font-family: "RalewayExtraBold", sans-serif; }

.hc_desc {
  margin-bottom: 44px; }

.hc_link {
  /*  // font-size: $size; */
  font-size: 1rem;
  border-radius: 40px;
  display: inline-block;
  background-color: #18a3dd;
  color: #fff;
  padding: 17px 35px;
  font-family: "LatoBold", sans-serif;
  -moz-transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

.hc_link:hover {
  background-color: #3eb7ea; }

.hc_watch {
  padding: 0 30px;
  /*  // font-size: $size; */
  font-size: 1rem;
  text-align: center;
  font-family: "LatoBold", sans-serif;
  color: #333740;
  text-transform: uppercase; }

.hc_watch:hover {
  color: #4a4f5c;
  text-decoration: underline; }

.hc_watch .fa {
  margin-left: 5px;
  /*  // font-size: $size; */
  font-size: 1.25rem;
  color: #18a3dd; }

/*  оформление мобильной версии меню и гамбургера */
.mobile_gamburger {
  padding: 0;
  margin: 0;
  width: 45px;
  height: 33px;
  position: relative; }

nav,
.navbar-toggle {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

nav {
  position: fixed;
  z-index: 10;
  background-color: rgba(24, 163, 221, 0.95);
  width: 100%;
  left: 0;
  top: 0;
  height: 100%;
  text-align: center;
  display: table;
  color: white; }

.nav-hide {
  display: none; }

nav ul {
  display: table-cell;
  vertical-align: middle;
  padding-left: 0 !important; }

nav ul li {
  list-style: none; }

nav ul li a {
  font-size: 35pt;
  color: inherit;
  font-weight: bolder;
  text-decoration: none; }

.navbar-toggle {
  position: absolute;
  top: 0;
  right: 0;
  width: 45px;
  height: 45px;
  z-index: 20;
  cursor: pointer; }

.bar1,
.bar2,
.bar3 {
  width: 100%;
  height: 3px;
  margin-bottom: 10px;
  background-color: #1c1f72;
  transition: all 0.3s ease-in-out; }

.fixed {
  position: fixed;
  /*позицианируем чтобы исчез скролл*/
  width: 100%;
  /*что бы верстка не складывалась по ширине*/ }

.navbar-on .bar1,
.navbar-on .bar2,
.navbar-on .bar3 {
  background-color: white; }

.navbar-on .bar1 {
  transform-origin: 10% 40%;
  transform: rotate(45deg); }

.navbar-on .bar3 {
  transform-origin: 10% 40%;
  transform: rotate(-45deg); }

.navbar-on .bar2 {
  background-color: transparent; }

